
import { gpf } from "@/utils/global-functions";
import {
  computed,
  defineAsyncComponent,
  defineComponent,
  nextTick,
  ref,
  watch,
} from "vue";

const ModalTemplate = defineAsyncComponent(
  () => import(`@/components/modal-template/Main.vue`)
);

const LabelWithColon = defineAsyncComponent(
  () => import(`@/components/label/WithColon.vue`)
);

const servicesV1AdminLearningCategories = async () =>
  import(`@/services/v1/admin/learning/categories`);

export default defineComponent({
  components: {
    ModalTemplate,
    LabelWithColon,
  },
  props: {
    showDetailCategory: {
      type: Boolean,
      default: false,
    },
    categoryCode: {
      type: String,
      default: "",
    },
  },
  setup(props, { emit }) {
    const modalId = "modal-detail-category";
    const categoryCode = computed(() => props.categoryCode);
    const detailCategory = ref({} as any);
    const loading = ref(false as boolean);

    const getDetailCategory = async () => {
      if (!categoryCode.value) return;

      const {
        getAdminLearningCategoryById,
      } = await servicesV1AdminLearningCategories();

      gpf.gLoading.show();
      loading.value = true;

      try {
        const hit = await getAdminLearningCategoryById(categoryCode.value);
        const resData = hit.response;

        console.log("resData getDetailCategory ", resData);

        await nextTick();

        detailCategory.value = resData;
      } catch (err: any) {
        console.log("err response getDetailCategory ", err, err?.response);

        gpf.handleEds(err);
      } finally {
        gpf.gLoading.hide();
        loading.value = false;
      }
    };

    const hideDetailCategory = () => {
      // detailCategory.value = {};

      emit("update:show-detail-category", false);
      emit("update:category-code", 0);
    };

    watch(
      () => props.showDetailCategory,
      (newVal) => {
        if (newVal) gpf.showModal(`#${modalId}`);
        gpf.onHideModal(`#${modalId}`, hideDetailCategory);
        getDetailCategory();
      }
    );

    const onCloseClick = () => {
      gpf.hideModal(`#${modalId}`);
    };

    return {
      modalId,
      detailCategory,
      gpf,
      loading,
      onCloseClick,
    };
  },
});
